import lotusAddress from '../../components/picker-address/lotusAddress.vue'
export default{
	data(){
		return{
			USER:{
				userDis:'北京市'
			},
			lotusAddressData:{
				visible:false,
				provinceName:'',
				cityName:'',
				townName:'',
			},
		}
	},
	components:{
		lotusAddress
	},
	onLoad() {
		
	},
	methods:{
		uploadImg(){   //修改头像
			uni.chooseImage({
			    count: 1, 
			    sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera'], //从相册选择
			    success: function (res) {
					console.log(res)
			        console.log(JSON.stringify(res.tempFilePaths));
			    },
				fail: function (err) {
					uni.showToast({
					    title: JSON.stringify(err),
						icon:'none',
					    duration: 2000
					});
				}
			});
		},
		changeDis() { //调用地址PICKER  //缺人回调
			this.lotusAddressData.visible = true;
		},
		choseValue(res){   //缺人回调
			console.log(res);
			this.lotusAddressData.visible = res.visible;
			this.lotusAddressData.provinceName = res.province;
			this.lotusAddressData.cityName = res.city;
			this.lotusAddressData.townName = res.town;
			this.USER.userDis = `${res.province} ${res.city} ${res.town}`; 
		}
	}
}
